import axios, { Axios, CreateAxiosDefaults } from 'axios'
import { BNify } from '../../core'
import { IntegrationClientModel } from '../integration-client.model'

export class UsualClient implements IntegrationClientModel {
  // Axios instance
  private axios: Axios

  constructor() {
    this.axios = this.initAxios()
  }

  /**
   * Axios initialization
   */
  private initAxios() {
    const options: CreateAxiosDefaults = {
      baseURL: 'https://app.usual.money/api/',
      headers: {
        'content-type': 'application/json',
      },
    }
    return axios.create(options)
  }

  // Methods

  public async getApr(tokenSymbol: string): Promise<number> {
    switch (tokenSymbol) {
      case 'USD0++':
        return this.getUsd0Apr()
      default:
        return 0
    }
  }

  public async getUsd0Apr(): Promise<number> {
    return this.axios
      .request({
        url: 'rewards/rates/USD0++',
        method: 'GET',
        responseType: 'json',
      })
      .then((response) =>
        BNify(response?.data.rewards[0].apr).times(100).toNumber()
      )
      .catch(() => 0)
  }
}
